html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 500;
}
main {
  min-height: calc(100vh - 293px);
}
/* –°—Ç–∏–ª–∏ –¥–ª—è –æ–±–Ω—É–ª–µ–Ω–∏—è –≤–µ—Ä—Ö–Ω–∏—Ö –æ—Ç—Å—Ç—É–ø–æ–≤ —É —ç–ª–µ–º–µ–Ω—Ç–æ–≤ */
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

/* –ö–ª–∞—Å—Å –¥–ª—è –æ–±–Ω—É–ª–µ–Ω–∏—è –±–∞–∑–æ–≤—ã—Ö —Å–≤–æ–π—Å—Ç–≤ —É —Å–ø–∏—Å–∫–æ–≤ (ul) */
ul {
  list-style: none;
  padding-left: 0;
  margin: 0;
}

/* –ö–ª–∞—Å—Å –¥–ª—è –æ–±–Ω—É–ª–µ–Ω–∏—è –±–∞–∑–æ–≤—ã—Ö —Å–≤–æ–π—Å—Ç–≤ —É —Å—Å—ã–ª–æ–∫ */
a {
  text-decoration: none;
  color: inherit;
}

/* –°–≤–æ–π—Å—Ç–≤–∞ –¥–ª—è –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ–≥–æ –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è –∫–∞—Ä—Ç–∏–Ω–æ–∫ */
img {
  display: block;
  max-width: 100%;
  height: auto;
}

/* –°–≤–æ–π—Å—Ç–≤–∞ –¥–ª—è –æ–±–Ω—É–ª–µ–Ω–∏—è –∫—É—Ä—Å–∏–≤–∞ —É address */
address {
  font-style: normal;
}
// –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä
.container {
  margin: 0 auto;
  padding: 0 20px;

  @media screen and (min-width: 320px) {
    width: 100%;
  }

  @media screen and (min-width: 768px) {
    width: 768px;
    padding: 0 32px;
  }

  @media screen and (min-width: 1280px) {
    padding: 0 32px;
    width: 1280px;
  }
}
